const FormCreator = (Type) => ({ input, meta, ...props })=>{
    const hasError = meta.error && (meta.touched);

    return (
        <div className={`${s.formControl} ${hasError ? s.error : ''}`}>
            <div>
                <Type {...input} {...props} />

            </div>
            {hasError && <span >{meta.error}</span>}
        </div>
    )
}
export default FormCreator;